.container{
  max-width: 700px;
  width: 100%;
  background-color: #fff;
  padding: 25px 30px;
  border-radius: 5px;
  margin-left: 400px;
  margin-top: 100px;
  margin-bottom:100px;
  box-shadow: 0 5px 10px rgba(0,0,0,0.15);
}
.container .title{
  font-size: 25px;
  font-weight: 500;
  position: relative;
  color:  #dd5211;;
}

.content form .user-details{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px 0 12px 0;
}
form .user-details .input-box{
  margin-bottom: 15px;
  width: calc(100% / 2 - 20px);
}
form .input-box span.details{
  display: block;
  font-weight: 500;
  margin-bottom: 5px;
}
.user-details .input-box input{
  height: 45px;
  width: 100%;
  outline: none;
  font-size: 16px;
  border-radius: 5px;
  padding-left: 15px;
  border: 1px solid #ccc;
  border-bottom-width: 2px;
  transition: all 0.3s ease;
}
.user-details .input-box input:focus,
.user-details .input-box input:valid{
  border-color:  #dd5211;;
}
 form .gender-details .gender-title{
  font-size: 20px;
  font-weight: 500;
 }
 form .category{
   display: flex;
   width: 80%;
   margin: 14px 0 ;
   justify-content: space-between;
 }
 form .category label{
   display: flex;
   align-items: center;
   cursor: pointer;
 }
 form .category label .dot{
  height: 18px;
  width: 18px;
  border-radius: 50%;
  margin-right: 10px;
  background: #d9d9d9;
  border: 5px solid transparent;
  transition: all 0.3s ease;
}
 #dot-1:checked ~ .category label .one,
 #dot-2:checked ~ .category label .two,
 #dot-3:checked ~ .category label .three{
   background: #dd5211;
   border-color: #d9d9d9;
 }
 form input[type="radio"]{
   display: none;
 }

 form .button {
   height: 40px;
   width: auto;
   border-radius: 5px;
   display: flex;
   border: none;
   color: #fff;
   font-size: 18px;
   font-weight: 500;
   letter-spacing: 1px;
   cursor: pointer;
   transition: all 0.3s ease;
   background: linear-gradient(135deg, #f53f08, #df9b1e);
 }
 form .button:hover{
  transform: scale(0.99);
  background: linear-gradient(-135deg,  #f53f08, #df9b1e);
  }

  form button{
    font-size: 20px;
    font-weight: bold;
    color: #fff;
    background-color: transparent;
    border: 0px;
    justify-content: center;
    padding-top: 7px;
    padding-left: 300px;
    
  }
 @media(max-width: 584px){
 .container{
  max-width: 100%;
  margin: 0;
  
}
form .user-details .input-box{
    margin-bottom: 15px;
    width: 100%;
  }
  form .category{
    width: 100%;
  }

  form .button {
    justify-content: center;
    padding-bottom: 10px;
    padding-right: 300px;
  }
  .content form .user-details{
    max-height: 300px;
    overflow-y: scroll;
  }
   .user-details::-webkit-scrollbar{
    width: 5px;
   
  }
 }
  
  @media(max-width: 459px){
  .container .content .category{
    flex-direction: column;
  }
}
