.navbar {
  background: linear-gradient(135deg, #f3f6f8, #ffffff);
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  box-shadow: 0 5px 10px rgba(0,0,0,0.15);;
  top: 0;
  z-index: 999;
}

.navbar:hover{
  background: linear-gradient(-135deg, #f3f6f8, #ffffff);;

}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  height: 125px;
  width: 150px;
  padding-top: 10px;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  display: flex;

} 


.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content:end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.search{
  display: flex;
  position: relative;

}

 .search Input{
  margin-top: 27px;
  width: 400px;
  height: 30px;
  border-radius: 20px;
  border: 1px solid #f5f5f5;
  padding: 10px;
  text-transform: capitalize;
  color: #9a9a9a;
}

.search Input::placeholder{
  color: #cecaca;
}

.search input:focus{
  outline: none;
}

.searchIcon{
  height: 60px;
  width: 20px;
  place-items: center;
  color:  #dd5211;
  margin-top: 12px;
  display: grid;
  position: absolute;
  right:10px;
  cursor: pointer;
  overflow: hidden;
  
}


.nav-links {
  color:  #dd5211;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 2px solid rgb(241, 234, 234);
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: rgb(20, 8, 8);
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background:#f5f5f5;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: -80px;
    left: -20px;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: rgb(20, 8, 8);
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: #fff;
    color: rgb(240, 9, 9);
    padding: 14px 20px;
    border: 5px solid #f5f5f5;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }

  .search{
    width: 60%;
    margin-left: 100px;
    
  }
}
